element {
    --main-bg-color: green;
}

body {
    width: 100%;
    background: linear-gradient(90deg, rgb(139, 136, 207) 0%, rgb(92, 92, 243) 35%, rgba(0,212,255,1) 100%);
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 28px;
    color: white;
    box-sizing: border-box;
    animation: flashes 1.5s linear 1.5s infinite;
}

.login-block {
    margin-top: 30vh;
    text-align: center;
}

.field-description {
    margin-bottom: 20px;
}

.user-login {
    margin-bottom: 20px;
    width: 15rem;
    height: 50px;
    font-size: 24px;
    border-radius: 4px;
    text-align: center;
    border: 1px solid blue;
}

.submit-button {
    margin-top: 20px;
    height: 45px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid blue;
    cursor: pointer;
}

.images-title,
.question-title,
.message-title {
    margin: 20px;
}

.title-image {
    max-height: 15vh;
}

.images-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(auto-fill);
    grid-gap: 1px;
    margin-top: 1vw;
    margin-left: 0%;
    min-width: 40vw;
    min-height: 55vh;
}

.preview-component .images-list {
    min-width: 100%;
    min-height: 100%;
}

.center-content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.question-image {
    padding: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgb(255, 0, 0);
}

    .question-image img {
        max-height: 200px;
        max-width: 200px;
    }

.sender {
    margin-top: 20vh;
}

.sender-answers {
    padding: 25px;
}

.json-generator-wrapper {
    margin-top: 1.5vh;
    width: 90vw;
    display: grid;
    max-height: 90vh;
    text-align: left;
    font-size: 20px;
    grid-template-columns: 1fr .5fr;
    column-gap: 1vw;
}

.component_wrapper,
.preview-component {
    padding: .5vw;
    max-height: 90vh;
    border: 1px solid black;
    border-radius: 10px;
}

.component_wrapper {
    display: grid;
    max-height: 90vh;
    grid-template-columns: 40% 60%;
}

.component,
.component-type {
    margin: .5vw;
    padding: 1vw;
    max-height: 90vh;
    overflow-y: auto;
    box-sizing: border-box;
    background-color: rgba(252, 251, 251, 0.4);
}

.сomponent-buttons {
    margin-top: 1.5vh;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

    .сomponent-buttons button {
        margin-top: 5px;
    }

.preview-component {
    overflow-y: auto;
    box-sizing: border-box;
    max-height: 90vh;
}

hr {
    margin-top: 2.5vh;
    height: 0.7px;
    background-color: lightgrey;
    border: none;
}

.json-generator-item-field {
    margin-bottom: 1.5vh;
}

.questions-list {
    margin-left: 30%;
}

.ant-radio-inner::after {
    background-color: --main-background-color;
}

.questions-list span {
    color: white;
}

::-webkit-scrollbar {
    width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #4fbdcc;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(128, 179, 209);
}

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
        background: rgb(224, 207, 207);
    }

@-webkit-keyframes flashes {
    0% {
        background: rgb(255, 0, 0);
    }

    50% {
        background: #d400ff;
    }

    100% {
        background: rgb(255, 0, 0);
    }
}

@keyframes flashes {
    0% {
        background: rgb(255, 0, 0);
    }

    1% {
        background: #d400ff;
    }

    51% {
        background: #d400ff;
    }

    52% {
        background: rgb(255, 0, 0);
    }

    100% {
        background: rgb(255, 0, 0);
    }
}

@media screen and (max-width: 1124px) {
    .json-generator-wrapper {
        grid-template-columns: 1fr;
        row-gap: 1vh;
        grid-template-rows: 1fr 1fr;
    }

    .question-image img {
        margin: 0 auto;
        width: 50%;
        height: 50%;
    }
}

@media screen and (max-width: 768px) {
    .images-title,
    .question-title,
    .message-title,
    .timer {
        font-size: medium;
    }

    .images-list {
        min-width: 35vw;
        min-height: 35vh;
    }
}
